.about {
  position: relative;
  margin: 2rem 0;
  padding: 1rem;
  &-back {
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    text-transform: uppercase;
    font-size: 15rem;
    color: #f3f3f3;
    letter-spacing: 3rem;
    z-index: -1;
  }
  &-block {
    padding: 2rem;
    border-radius: .5rem;
  }

  &-btn {
    display: block;
    min-width: 40%;
    margin: 0 auto;
    outline: none;
    padding: 1rem;
    font-size: 1rem;
    font-weight: bold;
    margin-top: .5rem;
    border: none;
    background-color: $color-main;
    color: $color-white;
    border-radius: .5rem;
  }
}

@media only screen and (max-width: 768px) {
  .about-back {
    font-size: 10rem;
  }
}

@media only screen and (max-width: 600px) {
  .about-back {
    font-size: 7rem;
  }
}
